import { useEffect } from "react";
import { Link, Navigate, useParams } from "react-router-dom";
import { resetCartAsync } from "../features/cart/CartSlice";
import { useDispatch, useSelector } from "react-redux";
import { selectLoggedInUser } from "../features/auth/AuthSlice";
import { resetCurrentOrder } from "../features/order/OrderSlice";

function OrderSucces({order}) {
  const params = useParams()
  // // const  user = useSelector(selectLoggedInUser)
  const dispatch = useDispatch()
  // const  id = user?.id
  // // console.log(user.id,"sd")
  useEffect(()=>{
    dispatch(resetCartAsync());
    dispatch(resetCurrentOrder())
  },[dispatch])
  return (
   <>
    <div className="dark">
  <div className="flex justify-center  items-center bg-gray-900 h-screen text-white">
    <div className="bg-gray-800  w-[300px] sm:w-[600px] p-6 md:mx-auto">
      <svg
        viewBox="0 0 24 24"
        className="text-green-400  w-16 h-16 mx-auto my-6"
      >
        <path
          fill="currentColor"
          d="M12,0A12,12,0,1,0,24,12,12.014,12.014,0,0,0,12,0Zm6.927,8.2-6.845,9.289a1.011,1.011,0,0,1-1.43.188L5.764,13.769a1,1,0,1,1,1.25-1.562l4.076,3.261,6.227-8.451A1,1,0,1,1,18.927,8.2Z"
        ></path>
      </svg>
      <div className="text-center">
        <h3 className="md:text-2xl text-base text-white font-semibold text-center">
          Order SuccesFully placed
        </h3>
        <p className="text-gray-400 my-2">
          Thank you for buy on over website
        </p>
        <p className="text-gray-100 text-xl lg:text-4xl  my-2">
         Order Number #{params?.id}.
        </p>
        <p>Have a great day!</p>
        <div className="py-10 text-center">
          <Link
            to="/myorders"
            className="px-12 bg-indigo-600 hover:bg-indigo-500 text-white font-semibold py-3"
          >
           Check Your Order 
          </Link>
        </div>
      </div>
    </div>
  </div>
</div></>

  );
}

export default OrderSucces;
